:root {
  /* COLORS */
  --blue_1: #0044BB;
  --blue_2: #1F9EF3;
  --blue_3: #2B50A2;
  --white_1: #FFFFFF;
  --white_2: #E7F5FF;
  --gray_1: #E7F5FF;
  --gray_2: #575757;
  --gray_3: #D9D9D9;
  --gray_4: #666676;
  --green_1: #00E55C;
  --black_1: #333333;
  --black_2: #0A0A0A;
  --black_3: #000000;
  --black_4: #171711;
}

/* FONTS */
/* Roboto */
/* Montserrat */
/* Inter */
*,
*:before,
*:after {
  box-sizing: border-box;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select,
  textarea,
  input {
    font-size: 1.6rem !important;
  }
}
img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

input[type=submit],
button {
  cursor: pointer;
}

hr {
  display: block;
  height: 0.1em;
  border: 0;
  border-top: 0.1em solid #ccc;
  margin: 1em 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

ol {
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat-Regular", "Arial", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
}

section,
footer,
main,
header,
.main-inner {
  width: 100%;
}

main {
  flex: 1 0 auto;
}

footer {
  width: 100vw;
  flex: 0 0 auto;
}

.wrapper {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #EBEDEF;
}

.container {
  width: 100%;
  max-width: 54rem;
  padding: 0 3rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .container {
    max-width: 72rem;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 90rem;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 114rem;
  }
}
@media (min-width: 1440px) {
