.burger {
  position: relative;
  left: 0;
  display: block;
  width: 3.2rem;
  height: 3.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: var(--blue_2);
  border-radius: 50%;
  margin-left: 2rem;
}
@media (min-width: 768px) {
  .burger {
    left: 0;
    top: 0.6rem;
  }
}
@media (min-width: 1440px) {
  .burger {
    display: none;
  }
}
.burger__line {
  position: absolute;
  width: 2rem;
  height: 0.2rem;
  background-color: var(--white_1);
  transition: all 0.3s ease;
}
.burger__line--top {
  top: 0.9rem;
}
.burger__line--middle {
  top: 50%;
  transform: translateY(-50%);
}
.burger__line--bottom {
  bottom: 0.9rem;
}

.burger--active .burger__line--top {
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}
.burger--active .burger__line--middle {
  opacity: 0;
}
.burger--active .burger__line--bottom {
  bottom: 50%;
  transform: translateY(50%) rotate(-45deg);
}

